  margin-left: 0;
  margin-right: 0;
}

.col {
  box-sizing: border-box;
  min-width: 0;
  margin-left: 15px;
  margin-left: var(--gutter);
  margin-right: 15px;
  margin-right: var(--gutter);
  width: 100%;
  flex-basis: 0;
  flex-grow: 1;
}

html {
  font-family: "Raleway";
  height: 100%;
}

body {
  background-color: #27272c;
  color: #fff;
  height: 100%;
  font-size: 1.025em;
  line-height: 1.4;
}

body.content-hidden {
  overflow: hidden;
}

img {
  display: block;
  max-width: 100%;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

strong {
  font-weight: 500;
}

input[type=radio] {
  display: none;
}

.custom-radio {
  display: inline-block;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 15px;
}

.custom-radio:hover span::before {
  transform: translate(-50%, -50%) scale(1.1);
}

input[type=radio] + span {
  position: relative;
  display: block;
  color: white;
  font-size: 1.2rem;
  cursor: pointer;
  user-select: none;
  padding-left: 25px;
}

input[type=radio] + span::before {
  transition: all 250ms cubic-bezier(0.4, 0.25, 0.3, 1);
  content: "";
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background-color: white;
  font-size: 0;
  position: absolute;
  top: 12px;
  left: 0;
  transform: translate(-50%, -50%);
}

input[type=radio] + span::after {
  transition: all 250ms cubic-bezier(0.4, 0.25, 0.3, 1);
  content: "";
  width: 0;
  height: 0;
  border-radius: 50%;
  background-color: white;
  position: absolute;
  top: 12px;
  left: 0;
  transform: translate(-50%, -50%);
}

input[type=radio]:checked + span::before {
  background-color: transparent;
  width: 22px;
  height: 22px;
  border-width: 2px;
  background-origin: border-box;
  background-clip: content-box, border-box;
  background-image: linear-gradient(to right bottom, #d367c1 10%, #dedf40 25%, #62cb5c 50%, #00bbcb 75%, #ab79d6 90%);
}

input[type=radio]:checked + span::after {
  width: 16px;
  height: 16px;
}

.header {
  padding: 10px 0;
}

.header__logo {
  width: 84px;
}

.quiz {
  padding-top: 5px;
  padding-bottom: 50px;
}

.quiz__slider {
  margin-top: 50px;
}

.quiz__slide {
  height: auto;
  background: #27272c;
}

.quiz__slide-content {
  border-style: solid;
  border-width: 0 0 0 5px;
  border-image: linear-gradient(to bottom, #d367c1 10%, #dedf40 25%, #62cb5c 50%, #00bbcb 75%, #ab79d6 90%) 1;
  padding: 20px 10px 20px 30px;
}

.quiz__slide-content h3 {
  margin-top: 0;
  font-size: 1.4em;
}

.quiz__slide-content .input-el {
  margin-bottom: 10px;
}

.quiz__slide-content .error {
  color: red;
  animation: shake-animation 4.72s ease infinite;
  transform-origin: 50% 50%;
}

.quiz button {
  background: #d367c1;
  color: #fff;
  font-weight: 500;
  padding: 7px 18px;
  border: none;
  appearance: none;
  border-radius: 4px;
  transition: 0.3s;
  cursor: pointer;
  display: inline-block;
  margin-top: 20px;
  user-select: none;
}

.quiz button:hover {
  background: #cd53b9;
}

.quiz button:hover::after {
  transform: translateX(2px) rotate(45deg);
}

.quiz button::after {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 6px;
  height: 6px;
  border-top: 2px solid #fff;
  border-right: 2px solid #fff;
  margin-left: 5px;
  transform: rotate(45deg);
  transition: 0.3s;
}

.quiz .new-test {
  display: none;
  background: #00bbcb;
}

.quiz .new-test:hover {
  background: #00a4b2;
}

.quiz .new-test:hover::after {
  transform: rotate(245deg);
}

.quiz .new-test::after {
  width: 16px;
  height: 16px;
  border-top: 1px solid #fff;
  border-right: 1px solid #fff;
  border-radius: 50%;
  transform: rotate(125deg);
}

.quiz__result {
  font-size: 1.4em;
  font-weight: 300;
}

@keyframes shake-animation {
  0% {
    transform: translate(0, 0);
  }

  1.78571% {
    transform: translate(5px, 0);
  }

  3.57143% {
    transform: translate(0, 0);
  }

  5.35714% {
    transform: translate(5px, 0);
  }

  7.14286% {
    transform: translate(0, 0);
  }

  8.92857% {
    transform: translate(5px, 0);
  }

  10.71429% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate(0, 0);
  }
}

@media (min-width: 768px) {
  .quiz {
    padding-top: 20px;
  }
}

@media (min-width: 992px) {
  :root {
    --mob-padding: 40px;
  }
}